<template>
  <div class="LybQrCode">
    <img
      :src="imgUrl"
      :style="{
        width: size,
        height: size,
      }"
    />
    <vue-qr
      :logoSrc="icon"
      :callback="test"
      :text="text"
      :size="1000"
      :bindElement="false"
    />
  </div>
</template>
<script>
//https://gitee.com/mirrors_soldair/node-qrcode
//#####!//
//
import vueQr from "vue-qr";
export default {
  name: "LybQrCode",
  components: { vueQr },
  props: {
    //
    text: {
      type: String,
      default: "",
    },
    icon: {
      default: require("./img/icon.png"),
    },
    size: {
      type: String,
      default: "25vh",
    },
  },
  data() {
    return {
      imgUrl: "",
    };
  },
  methods: {
    test(dataUrl) {
      this.imgUrl = dataUrl;
    },
  },
};
</script>
