<template>
  <div class="lyb-upload-single">
    <transition-group class="index" name="del">
      <!--  -->
      <div
        class="imgBox"
        v-for="(item, index) in fileList"
        @mouseenter="show(index)"
        @mouseleave="currrentIndex = null"
        @touchstart="show(index)"
        @touchend="currrentIndex = null"
        :key="item.id"
      >
        <!--  -->
        <div
          class="mask"
          :style="{ height: item.pre == 0 ? '100%' : item.pre }"
        >
          <span v-if="!item.isFinish">
            {{
              item.pre === 0
                ? item.isUpload === true
                  ? ""
                  : item.isFail
                  ? ""
                  : ""
                : item.pre == "100%"
                ? ""
                : item.pre
            }}
          </span>
          <marquee v-if="!item.isUploading">{{ item.name }}</marquee>
          <span v-if="!item.isUploading && !disabled">{{
            typeof item.size == "string" ? item.size : item.size[2]
          }}</span>
        </div>
        <!--  -->
        <div
          class="finish"
          :class="{ failBgc: !item.isFinish }"
          v-show="item.isFinish || item.isFail"
          v-if="!disabled"
        >
          <lyb-icon
            style="transform: rotate(-45deg)"
            :imgUrl="
              item.isFinish
                ? require('./img/finish.svg')
                : require('./img/fail.svg')
            "
            size="12px"
            bottom="3px"
          ></lyb-icon>
        </div>
        <!--  -->
        <img :src="item.url" />
        <!--  -->
        <div class="operation" v-show="currrentIndex == index">
          <img
            v-if="!disabled"
            src="./img/del.svg"
            @click="delImg(index)"
            alt=""
          />
          <img src="./img/show.svg" @click="preview(index)" alt="" />
        </div>
      </div>

      <!--  -->
      <div class="box1" key="a">
        <input
          v-if="!disabled"
          type="file"
          ref="file"
          :id="id"
          style="display: none"
          @change="filePreview()"
          accep="image"
          :value="[]"
        />
        <label :for="id" v-show="select_show">
          <div class="select" :class="{ disabled: disabled }"></div>
        </label>
      </div>
    </transition-group>
    <transition name="show">
      <div
        class="maskImg"
        v-show="is_show_big_img"
        @click="is_show_big_img = false"
        :style="{ backgroundImage: 'url(' + preview_img + ')' }"
      ></div>
    </transition>
  </div>
</template>
<script>
import axios from "axios";
import { $getFileSuf, $fmtByte, $urlFileType } from "./lyb.js";
import lybIcon from "./childComp/lyb-icon/lyb-icon.vue";
export default {
  name: "LybUploadSingle",
  props: {
    // 
    fileUrl: {
      type: String,
      default: "",
      // default: "",
    },
    // 
    fileName: {
      type: String,
      default: "",
    },
    /* id */
    id: {
      type: String,
      default: "select",
    },
    /*  */
    url: {
      type: String,
      default: "",
    },
    /*  */
    headers: {
      type: Object,
      default() {
        return {
          token: localStorage.getItem("token"),
        };
      },
    },
    /*  */
    size: {
      type: Number,
      default: 0,
    },
    /* imagevideo */
    suffix: {
      default() {
        return false;
      },
    },
    /*  */
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  components: { lybIcon },
  data() {
    return {
      preview_img: "", //
      fileList: [], //
      fake_files: [], //
      currrentIndex: null, //
      select_show: true, //
      is_show_big_img: false, //
    };
  },
  computed: {
    //##########//
    fileIcon() {
      return function (suffix) {
        let img = "jpg,jpeg,png,gif,svg",
          music = "mp3,ogg",
          video = "mp4",
          txt = "txt",
          pdf = "pdf",
          word = "doc,docx",
          excel = "xls,xlsx",
          ppt = "ppt,pptx",
          compress = "zip,7z",
          software = "exe,apk";
        if (img.includes(suffix)) return "img";
        if (compress.includes(suffix)) return "compress";
        if (excel.includes(suffix)) return "excel";
        if (music.includes(suffix)) return "music";
        if (pdf.includes(suffix)) return "pdf";
        if (ppt.includes(suffix)) return "ppt";
        if (software.includes(suffix)) return "software";
        if (txt.includes(suffix)) return "txt";
        if (video.includes(suffix)) return "video";
        if (word.includes(suffix)) return "word";
        return "unknown";
      };
    },

    //##########//
    existFinish() {
      return this.fileList.some((item) => {
        return item.isFinish;
      });
    },
  },
  created() {
    if (!this.fileUrl) return;
    this.fake_files = [{ name: this.fileName, url: this.fileUrl }];
    this.fileList.push({
      id: this.fileList.length + new Date().getTime(), // id
      name: this.fileName, //
      size: "", //
      url: this.fileUrl, //
      time: new Date().getTime(), //
      pre: "100%", //
      isUpload: 1, //
      isUploading: false, //
      isFinish: true, //
      isFail: false, //
      cancel: null, //
    });
    /*  */
    this.select_show = false;
  },
  methods: {
    //##########//
    dragover(e) {
      console.log("");
      e.preventDefault();
    },

    //##########//
    dragleave() {
      console.log("");
    },

    //##########//
    show(id) {
      this.currrentIndex = id;
    },

    //##########//
    preview(index) {
      this.preview_img = this.fileList[index].url;
      this.is_show_big_img = true;
    },

    //##########//
    delImg(index) {
      if (this.fileList[index].isFinish) {
        this.fake_files.splice(index, 1);
        this.$emit("update:fileUrl", "");
        this.$emit("finish", { name: this.fileName, src: "", id: this.id });
      }
      // 
      this.select_show = true;
      //
      this.fileList[index].cancel && this.fileList[index].cancel();
      this.fileList.splice(index, 1); //
      this.currrentIndex = null; //
    },

    //##########//
    filePreview(file = false) {
      //
      const fileList = file || this.$refs.file.files;
      // 
      let failFiles_suffix = [];
      // 
      let failFiles_size = [];
      // 
      let failFiles_suffix_pass = [];
      // 
      let files = [];

      /*  */
      [...fileList].forEach((item) => {
        // 
        if (this.suffix) {
          // 
          if ($urlFileType(item.name, this.suffix)) {
            failFiles_suffix_pass.push(item);
          } else {
            failFiles_suffix.push(item.name);
          }
        } else {
          failFiles_suffix_pass.push(item);
        }
      });
      //  
      if (failFiles_suffix.length) {
        alert(
          `${
            this.suffix
          }\n${failFiles_suffix.join("\n")}`,
        );
        failFiles_suffix = [];
      }
      //  
      [...failFiles_suffix_pass].forEach((item) => {
        if (item.size > this.size * 1024 * 1024 && this.size !== 0) {
          failFiles_size.push(item.name);
        } else {
          files.push(item);
        }
      });
      // 
      if (failFiles_size.length) {
        alert(
          `${
            this.size
          }MB\n${failFiles_size.join("\n")}`,
        );
        failFiles_size = [];
        failFiles_suffix_pass = [];
      }
      //
      let icon = {
        compress: require("./icon/compress.svg"),
        excel: require("./icon/excel.svg"),
        music: require("./icon/music.svg"),
        pdf: require("./icon/pdf.svg"),
        ppt: require("./icon/ppt.svg"),
        software: require("./icon/software.svg"),
        txt: require("./icon/txt.svg"),
        unknown: require("./icon/unknown.svg"),
        video: require("./icon/video.svg"),
        word: require("./icon/word.svg"),
      };

      //fileList
      [...fileList].forEach((item) => {
        if (
          (item.size > this.size * 1024 * 1024 && this.size !== 0) ||
          (this.suffix && !$urlFileType(item.name, this.suffix))
        )
          return;
        this.select_show = false;
        //
        let type = this.fileIcon($getFileSuf(item.name));
        //
        this.fileList.push({
          id: this.fileList.length + new Date().getTime(), // id
          name: this.fileName, //
          size: $fmtByte(item.size), //
          url: type == "img" ? window.URL.createObjectURL(item) : icon[type], //
          time: new Date().getTime(), //
          pre: 0, //
          isUpload: false, //
          isUploading: false, //
          isFinish: false, //
          isFail: false, //
          cancel: null, //
          file: item, //
        });
      });
      if (
        this.fileList.some((item) => {
          return item.isUploading;
        })
      )
        return;
      this.uploadClick();
      /*  */
      this.$refs.file.value = "";
    },

    //##########//
    async uploadClick() {
      // ()
      this.fileList.forEach((item) => {
        if (item.isUpload === false) item.isUpload = true;
      });

      //

      while (
        this.fileList.some((item) => {
          return item.isUpload !== 1;
        })
      ) {
        //
        let file = this.fileList.find((item) => {
          return item.isUpload !== 1;
        });

        //
        await this.uploadFile(file);
      }
    },

    //##########//
    uploadFile(file) {
      const that = this;
      return new Promise((resolve) => {
        file.isUploading = true; //
        //
        const data = new FormData();
        data.append("file", file.file);
        //#
        axios
          .post(
            this.url,
            //
            data,
            {
              headers: this.headers,

              //
              onUploadProgress(pre) {
                let progress = Math.round((pre.loaded / pre.total) * 100);
                file.pre = progress + "%";
                that.$emit("progress", progress);
              },

              //
              cancelToken: new axios.CancelToken((c) => {
                // cancel 
                file.cancel = c;
              }),
            },
          )
          .then((res) => {
            this.$emit(
              "update:fileUrl",
              "res.data.data.src",
            );
            this.fake_files.push({
              name: this.fileName,
              url: "res.data.data.src",
            }); // 
            this.$emit("finish", {
              name: this.fileName,
              src: "res.data.data.src",
              id: this.id,
            });

            resolve(res);
            file.cancel = null; //
            file.isFinish = true; //
            file.isUpload = 1; //
            file.isUploading = false; //
          })
          .catch((err) => {
            this.$emit("finish", "fail");
            resolve(err.message == undefined ? "" : "");
            file.cancel = null; //
            file.isFail = true; //
            file.isUpload = 1; //
            file.isUploading = false; //
            file.pre = 0; //0
          });
      });
    },
    //##########//
    clear() {
      this.fileList.forEach((item) => {
        item.cancel && item.cancel();
      });
      this.fileList = [];
      this.fake_files = [];
      this.select_show = true;
      this.$emit("finish", {
        name: this.fileName,
        src: "",
        id: this.id,
      });
    },
  },
};
</script>
<style lang="less" scoped>
.lyb-upload-single,
.index {
  display: flex;
  flex-wrap: wrap;
  .imgBox {
    width: 100px;
    height: 100px;
    position: relative;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    align-items: center;
    margin: 0 5px 5px 0;
    overflow: hidden;
    border: 2px solid #ddd;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .mask {
      display: flex;
      flex-direction: column;
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      overflow: hidden;
      span {
        width: 90%;
        text-align: center;
        &:nth-of-type(1) {
          color: #fff;
          font-size: 16px;
        }
        &:nth-of-type(2) {
          color: #ccc;
          font-size: 14px;
        }
      }
      marquee {
        display: flex;
        align-items: center;
      }
    }
    .finish {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      right: -17px;
      top: -7px;
      width: 46px;
      height: 26px;
      background-color: #13ce66;
      transform: rotate(45deg);
    }
    .operation {
      position: absolute;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      inset: 0;
      background-color: rgba(0, 0, 0, 0.75);
      img {
        cursor: pointer;
        width: 25px;
        height: 25px;
      }
    }
  }

  .box1 {
    flex-shrink: 0;
    .select {
      width: 100px;
      height: 100px;
      border: 2px dashed #ccc;
      background: no-repeat url("./img/add.svg") center;
      background-size: 25%;
      &:hover {
        background-color: #eee;
      }
      &:active {
        background-color: #ddd;
      }
    }
  }

  .maskImg {
    position: fixed;
    inset: 0;
    background: no-repeat center center;
    background-color: rgba(0, 0, 0, 0.75);
    background-size: contain;
    z-index: 999;
  }
}

.disabled {
  cursor: not-allowed;
}

.failFc {
  color: #ff4040 !important;
}
.failBgc {
  background-color: #ce1313 !important;
}

/*  */
.del-enter {
  transform: rotate(360deg) scale(0);
  opacity: 0;
}
.del-leave-active {
  margin: 0 !important;
  padding: 0 !important;
  width: 0 !important;
  transform: rotate(-360deg) scaleY(0);
  overflow: hidden;
}

.del-enter-active,
.del-leave-active {
  transition: all 0.5s;
}

/*  */
.show-enter {
  opacity: 0;
}
.show-leave-active {
  opacity: 0;
  overflow: hidden;
}

.show-enter-active,
.show-leave-active {
  transition: all 0.5s;
}
</style>
