<template>
  <div class="demoFull">
    <lybPdrPulSingle
      class="scroll"
      @refreshing="refreshData"
      @loading="addData"
      themeColor="white"
      :status="status"
      ref="scroll"
      bottom="25px"
      top="75px"
    >
      <div class="content">
        <li v-for="(item, index) in data" :key="index" ref="card">
          {{ item }}
        </li>
      </div>
    </lybPdrPulSingle>
  </div>
</template>
<script>
export default {
  name: "demo",
  data() {
    return {
      data: [],
      getData: [
        "",
        "",
