        "",
        "",
        "",
        "",
      ],
      total: 0,
      page: 0,
      limit: 20,
      totalPage: 0,
      status: "", // 
    };
  },
  created() {
    this.total = this.getData.length;
    this.totalPage = Math.floor(this.total / this.limit);
  },
  methods: {
    //########//
    getDataReq(page) {
      const _this = this;
      return new Promise((reslove) => {
        setTimeout(() => {
          if (page <= _this.totalPage) {
            _this.data.push(...this.getData.splice(0, _this.limit));
          } else if (_this.getData.length) {
            _this.data.push(..._this.getData);
          }
          if (_this.getData.length) {
            reslove(true);
          } else {
            reslove(null);
          }
          console.log(this.data);
        }, 1000);
      });
    },
    //########//
    refreshData() {
      console.warn("");
      this.page = 1;
      this.data = [];
      this.getDataReq(1)
        .then((res) => {
          // 
          this.$refs.scroll.refreshSuccess(res);
        })
        .catch((err) => {
          // 
          this.$refs.scroll.refreshSuccess(err);
        });
    },
    //########//
    addData() {
      console.warn("");
      let page = this.page;
      this.getDataReq(page)
        .then((status) => {
          // 
          this.$refs.scroll.loadSuccess(status);
          this.page++;
        })
        .catch((err) => {
          // 
          this.$refs.scroll.loadSuccess(err);
        });
    },
  },
};
</script>
<style scoped lang="less">
.demoFull {
  overflow: hidden;
  .scroll {
    .content {
      li {
        display: flex;
        justify-content: center;
        align-items: center;
        word-break: break-all;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        height: 100px;
        padding: 0 35px;
        color: #fff;
        list-style: none;
        font-size: 25px;
      }
    }
  }
}
