<template>
  <div>
    <slot
      :text="text"
      :start="start"
      :clearTimer="clearTimer"
      :disabled="disabled"
    ></slot>
  </div>
</template>
<script>
export default {
  name: "LybValidationCountdown",
  data() {
    return {
      disabled: false,
      text: "",
      timer: null,
    };
  },
  methods: {
    start() {
      this.disabled = true;
      this.countDown = 10;
      this.text = `${this.countDown}`;
      this.timer = setInterval(() => {
        this.countDown--;
        this.text = `${this.countDown}`;
        if (this.countDown === 0) {
          clearInterval(this.timer);
          this.text = "";
          this.disabled = false;
        }
      }, 1000);
    },
    //##########//
    clearTimer() {
      clearInterval(this.timer);
      this.disabled = false;
      this.text = "";
    },
  },
};
</script>
