<template>
  <div class="demoFull flex">
    <div class="box">
      <span>{{ beforeFile }}</span>
      <span>{{ afterFile }}</span>
    </div>
    <label for="a" class="btn"></label>
    <input
      v-show="false"
      type="file"
      id="a"
      v-imageOptimizer="{
        width: 1000, //
        ratio: 0.1, //
        maxsize: 1024, //
        /*  */
        success,
      }"
    />
  </div>
</template>
<script>
//##########//
//{  }
import { $fmtByte } from "@/utils/lyb.js";
export default {
  name: "ImageOptimizer",
  data() {
    return {
      before_file: 0,
      after_file: 0,
    };
  },
  computed: {
    beforeFile() {
      return $fmtByte(this.before_file.size)[2];
    },
    afterFile() {
      return $fmtByte(this.after_file.size)[2];
    },
  },
  methods: {
    success(data, file, base, init) {
      this.before_file = init;
      this.after_file = file;
      console.log({ form: data, file: file, base64: base });
      const a = document.createElement("a");
      a.style.display = "none";
      a.download = ".jpg";
      a.href = base;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
    },
  },
};
</script>
<style lang="less" scoped>
.demoFull {
  .box {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    span {
      color: #fff;
      font-size: 3vw;
    }
  }
}
</style>
