-vue
<template>
  <div class="demo demoFull flex">
    <h1></h1>
    <transition-group
      style="display: grid; width: 75%; height: 75%; grid-gap: 5px"
    >
      <!--
      xcountXycountY
     -->
      <LybGridNumber
        class="LybGridNumber"
        v-for="(item, index) in box"
        :x="item[0]"
        :countX="item[1]"
        :y="item[2]"
        :countY="item[3]"
        :key="index + '1'"
        >{{ index + 1 }}</LybGridNumber
      >
    </transition-group>
  </div>
</template>
<script>
/**
 * 
 * 
 * 
 */
export default {
  name: "demo",
  props: {},
  data() {
    return {
      boxs: {
        /**
         * 
         * 
         * 
         */
        500: [
          [1, 1, 1, 2],
          [1, 2, 3, 1],
          [1, 3, 4, 1],
          [2, 2, 1, 1],
          [3, 1, 3, 1],
          [3, 1, 2, 1],
          [2, 1, 2, 1],
        ],
        800: [
          [1, 1, 1, 2],
          [2, 1, 1, 3],
          [1, 1, 3, 1],
          [1, 3, 4, 1],
          [4, 1, 2, 3],
          [3, 2, 1, 1],
          [3, 1, 2, 2],
        ],
        1200: [
          [1, 4, 1, 1],
          [5, 2, 1, 2],
          [7, 2, 1, 1],
          [1, 2, 2, 2],
          [3, 2, 2, 1],
          [3, 6, 3, 1],
          [7, 2, 2, 1],
        ],
      },
      /*  */
      box: [],
    };
  },
  mounted() {
    this.fn();
    window.addEventListener(
      "resize",
      function () {
        this.fn();
      }.bind(this),
    );
  },
  methods: {
    fn() {
      const width = document.documentElement.clientWidth;
      Object.keys(this.boxs).forEach((item) => {
        if (width > Number(item)) {
          this.box = this.boxs[item];
        } else if (width < 500) {
          this.box = [
            [1, 1, 1, 3],
            [1, 4, 4, 1],
            [5, 2, 1, 3],
            [3, 1, 3, 1],
            [2, 1, 1, 2],
            [2, 3, 2, 1],
            [4, 2, 1, 2],
            [3, 2, 1, 1],
          ];
        }
      });
    },
  },
};
</script>
<style scoped lang="less">
.demo {
  flex-direction: column;
  h1 {
    color: #fff;
  }
  background-color: #000;
  .LybGridNumber {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #fff;
    color: #fff;
    font-size: 50px;
    font-family: "";
    transition: all 3s;
  }
}
</style>
